import React from 'react';

import CardInfo from './CardInfo'
function Card(props){

    return(
        <div className="d-inline-block l-card" onClick={(e) => props.onClick(props.item)} >
            <img className="l-card-image" src={props.item.imgSrc} alt={props.item.imgSrc} />
            { props.item.selected && <CardInfo title={props.item.title} subtitle={props.item.subtitle}/>}
        </div>
    );


}

export default Card;