import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

import Card from './Card'
import academica from '../assets/academica.png'
import laboral from '../assets/laboral.png'
import objetivo from '../assets/objetivo.png'
import lenguajes from '../assets/lenguajes.png'

class Carousel extends React.Component {
    constructor (props){
        super(props); 
        this.state = {
            items:[
                {
                    id:0,
                    title:'Objetivo',
                    subtitle:'Obtener la experiencia de un trabajo para fortalecer mi crecimiento personal y profesional.',
                    imgSrc: objetivo,
                    selected: false
                },
                {
                    id:1,
                    title:'Experiencia',
                    subtitle:'Actualmente no cuento con experiencia laboral, pero estoy abierto a iniciar en su empresa.',
                    imgSrc: laboral,
                    selected: false
                },
                {
                    id:2,
                    title:'Formación Académica',
                    subtitle:'Actualmente cursando el 4to año de la carrera de Ingeniería en Sistemas y Computación.',
                    imgSrc: academica,
                    selected: false
                },
                {
                    id:3,
                    title:'Lenguajes',
                    subtitle:'C/C++ | JAVA | PYTHON | VISUAL BASIC',
                    imgSrc: lenguajes,
                    selected: false
                }
            ]
        }
    }

    handleCardClick = (id, card) => {
    console.log(id);

        let items = [...this.state.items];
        items[id].selected = items[id].selected ? false : true;

        items.forEach(item =>{
            if(item.id !== id){
                item.selected = false;
            }
        });

        this.setState({
            items
        }); 
    } 

    makeItems = (items) =>{
        return items.map(item => {
            return <Card item ={item} onClick={(e => this.handleCardClick(item.id, e))} key={item.id}/>
        })
    }

    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }
    
}

export default Carousel;