import React from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Axios from 'axios';

import Hero from '../components/Hero';
import Content from '../components/Content';

class ContactPage extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            name:'',
            email: '',
            message: '',
            disabled: false,
            emailSent: null
        }
    }

    handleChange = (event) =>{
        
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        
        this.setState({
            [name]: value
        })
    }

    handleSubmit = (event) =>{
        event.preventDefault();

        this.setState({
            disabled: true
        });

        Axios.post('http://localhost:3030/api/email', this.state)
            .then(res =>{
                if(res.data.success){
                    this.setState({
                        disabled: true,
                        emailSent: true 
                    });
                }
                else{
                    this.setState({
                        disabled: false,
                        emailSent: false
                    });
                }
            })
            .catch(err =>{
                this.setState({
                    disabled: false,
                    emailSent: false
                });
               // console.log(err);
            })
    }


    render(){

        return(
            <div>
                <Hero title={this.props.title} />
                <Content>
                    <Form onSubmit={this.handleSubmit}>

                        <Form.Group>
                            <Form.Label htmlFor="full-name">Nombre Completo</Form.Label>
                            <Form.Control id= "full-name" name="name" type="text" value={this.state.name} onChange={this.handleChange}/>
                        </Form.Group>

                        <Form.Group>
                            <Form.Label htmlFor="email">Email</Form.Label>
                            <Form.Control id= "email" name="email" type="email" value={this.state.email} onChange={this.handleChange}/>
                        </Form.Group>

                        <Form.Group>
                            <Form.Label htmlFor="message">Mensaje</Form.Label>
                            <Form.Control id= "message" name="message" as="textarea" row="3" value={this.state.message} onChange={this.handleChange}/>
                        </Form.Group>
                        
                        <Button className="d-inline-block" variant="primary" type="submit" disabled={this.state.disabled}>
                              Enviar
                        </Button>

                        {this.state.emailSent === true && <p className="d-inline success-msg">Correo enviado satisfactoriamente</p>}
                        {this.state.emailSent === false && <p className="d-inline err-msg">No se ha podido enviar el correo</p>} 

                    </Form>
                </Content>
            </div>
            
        );

    }
}

export default ContactPage;