import React from 'react';
import Row from 'react-bootstrap/Row';

import Content from '../components/Content';
import Hero from '../components/Hero';
import Button from 'react-bootstrap/Button';

function AboutPage(props){
    return(
        <div className="f-height">
            <Hero title={props.title} />
            <Content>
                <p>¡Hola! , mi nombre es Luis y soy un entusiasta de la tecnología, me gusta mantenerme al día con las noticias de los los avances tecnológicos, mis temas preferidos son todos aquellos que involucren el uso de IA en ellos.</p> 
                <p>Actualmente estudio ingeniería en sistemas y computación en la Universidad Tecnológica de Panamá donde me desempeño como miembro y actual vicepresidente el grupo estudiantil Eurus en donde aporto en la creación de material para nuestras redes sociales.</p>

            </Content>
            <Row>
                <Button className="center c-mb" href={props.link} target="_blank" variant="primary">
                        Conocer más
                </Button>
            </Row>

        </div>
    );
}

export default AboutPage; 