import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ContactPage from './pages/ContactPage';

class App extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      title: "Luis Rosero",
      headerLinks: [
        {title: "Inicio", path: "/"},
        {title: "Sobre Mí", path: "/about"},
        {title: "Contacto", path: "/contact"}
      ],
      home:{
        title: "Ser perseverante",
        subtitle: "Trabajar con honestidad, esfuerzo y ambición por aprender.",
        text: "Observa mi descripción aquí debajo."
      },
      about:{
        title: "Sobre Mí",
        link: 'https://www.linkedin.com/in/luis-rosero-reyna-12219116a/'
      },
      contact:{
        title: "Hablemos",
      }
    }
  }

  render(){
    return (
      <Router>
        <Container className="p=0" fluid={true}>
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>Luis Rosero</Navbar.Brand>
            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle"/>
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Inicio</Link>
                <Link className="nav-link" to="/about">Sobre Mí</Link>
                <Link className="nav-link" to="/contact">Contacto</Link>

              </Nav>
            </Navbar.Collapse>
          </Navbar>

          <Route path="/" exact render={() => <HomePage title={this.state.home.title} subtitle={this.state.home.subtitle} text={this.state.home.text} />} />
          <Route path="/my-project" exact render={() => <HomePage title={this.state.home.title} subtitle={this.state.home.subtitle} text={this.state.home.text} />} />
          <Route path="/about" render={() => <AboutPage title={this.state.about.title} link={this.state.about.link}/>} />
          <Route path="/contact" render={() => <ContactPage title={this.state.contact.title} />} />

          <Footer />

        </Container>
      </Router>
    );
  }
}

export default App;
